/**
 * nodejs的交互，实现的功能
 * 1. node 节点的点击事件
 * 2. node 节点的拖拽效果
 * 3. 拖拽过程和拖拽结束后，数据的同步
 */
import { mapGetters, mapMutations } from "vuex";

/**
 * tips:缩放系数 1/scale
 */

export default {
    computed: {
        ...mapGetters(["actEditPlugin", "getPosition"]),
    },
    directives: {
        drag(el, binding, vnode) {
            /**
             * v-drag
             * 1. 当 node 点击的时候触发
             * 2. 根据 node 的 move 属性来判断是否被选中，false 直接返回
             * 3. 利用 getters 设置的 actEditPlugin 返回正在编辑的插件
             * 4. 移动结束后才修改 page 
             */

            let dragBox = el; // 保存引用
            let _this = vnode.context; //vue上下文

            dragBox.onmousedown = e => {

                e.preventDefault();
                //算出鼠标相对元素的位置
                let disX = e.clientX - dragBox.offsetLeft,
                    disY = e.clientY - dragBox.offsetTop;

                if (!_this.actEditPlugin) return
                let _style = _this.actEditPlugin.wrapStyle,
                    left = _style.left,
                    top = _style.top;

                document.onmousemove = e => {

                    //元素的位置
                    if (dragBox.getAttribute('move') == 'true') {


                        left = e.clientX - disX;
                        top = e.clientY - disY;


                        /**
                         * 贴合代码 --- 后续优化
                         */
                        let pos = _this.getPosition;
                        let dragWidth = dragBox.offsetWidth,
                            dragHeight = dragBox.offsetHeight;

                        pos.x.map(it => {
                            let linePosLeft1 = it - 100,
                                linePosLeft2 = it - 100 - dragWidth;
                            let dis1 = left - linePosLeft1,
                                dis2 = left - linePosLeft2;
                            if (dis1 < 6 && dis1 > -6) {
                                left = linePosLeft1
                            }
                            if (dis2 < 6 && dis2 > -6) {
                                left = linePosLeft2
                            }
                        })
                        pos.y.map(it => {
                            let linePosTop1 = it - 100,
                                linePosTop2 = it - 100 - dragHeight;
                            let dis1 = top - linePosTop1,
                                dis2 = top - linePosTop2;
                            if (dis1 < 6 && dis1 > -6) {
                                top = linePosTop1
                            }
                            if (dis2 < 6 && dis2 > -6) {
                                top = linePosTop2
                            }
                        })




                        //移动当前元素
                        dragBox.style.left = left + "px";
                        dragBox.style.top = top + "px";
                    }

                };
                document.onmouseup = () => {

                    /**
                     * tips:松开鼠标后才更新数据，不然移动过程的重复运算非常消耗cpu
                     */
                    _style.left = left;
                    _style.top = top;


                    document.onmousemove = null;
                    document.onmouseup = null;
                    if (typeof dragBox.releaseCapture !== 'undefined') {
                        dragBox.releaseCapture()
                    }
                };
            };
        }
    },
    methods: {
        ...mapMutations(["changeActPluginUid"]),
        nodeClick(e) {

            /**
             * 点击元素
             * 1. 获取元素的uid
             * 2. 改变store里面的actPluactginUid
             * 3. 改变 node 的 move
             */
            let target = e.currentTarget;
            let actPluactginUid = target.dataset.uid || false;
            this.changeActPluginUid(actPluactginUid);


            let allNode = document.getElementsByClassName("node");
            allNode.forEach(it => {
                it.setAttribute("move", false);
            })
            target.setAttribute("move", true);
        },
        bgClick() {
            this.changeActPluginUid(false);
            let allNode = document.getElementsByClassName("node");
            allNode.forEach(it => {
                it.setAttribute("move", false);
            })
        },

    },
}