<script>
/**
 * node-wrap
 * 1. 动态渲染所有使用到的组件
 * 2. 页面的视图层，联动组件控制和编辑器
 */

import { mapState } from "vuex";
// node节点的交互
import nodeMinxs from "../../../core/plugins/node-minxs";
export default {
    name: "node-wrap",
    mixins: [nodeMinxs],
    data() {
        return {
            pageStyle: {}
        };
    },
    render() {
        return (
            <div class="page-view">
                <div
                    style={this.pageStyle}
                    class="page-bg"
                    onClick={this.bgClick}
                ></div>
                {this.pluginsSytle.map(ele => {
                    return (
                        <div
                            class="node"
                            onClick={this.nodeClick}
                            v-drag
                            move="false"
                            data-uid={ele.uid}
                            style={ele.wrapStyle}
                        >
                            <div
                                class="control"
                                //   style={this.plugin.data.style}
                                //   onClick={this.editPlugin.bind(this)}
                            >
                                <span class="circle circle-top" />
                                <span class="circle circle-left" />
                                <span class="circle circle-right" />
                                <span class="circle circle-bottom" />
                                <span class="circle circle-topleft" />
                                <span class="circle circle-topright" />
                                <span class="circle circle-bottomleft" />
                                <span class="circle circle-bottomright" />
                            </div>
                            {
                                <ele.name
                                    params={ele}
                                    style={ele.nodeStyle}
                                ></ele.name>
                            }
                        </div>
                    );
                })}
            </div>
        );
    },
    computed: {
        ...mapState({
            page: state => state.page.pageInfo
        })
    },
    watch: {
        page: {
            handler(val) {
                /**
                 * 修改背景
                 */
                this.pageStyle = this.shallowCopy(val).style;
                this.num2px(this.pageStyle);

                /**
                 * 修改插件
                 */
                this.pluginsSytle = this.shallowCopy(val.plugins);
                this.pluginsSytle.map(it => {
                    this.num2px(it.wrapStyle);
                    this.num2px(it.nodeStyle);
                });
            },
            deep: true,
            immediate: true
        }
    },

    methods: {
        shallowCopy(val) {
            return JSON.parse(JSON.stringify(val));
        },
        num2px(obj) {
            if (Object.prototype.toString.call(obj) !== "[object Object]")
                return;
            Object.keys(obj).forEach(pro => {
                if (
                    pro == "width" ||
                    pro == "height" ||
                    pro == "top" ||
                    pro == "left" ||
                    pro == "border-width"
                ) {
                    obj[pro] = obj[pro] + "px";
                } else if (pro == "transform") {
                    obj[pro] = "rotate(" + obj[pro] + "deg)";
                } else if (pro == "border-radius") {
                    obj[pro] = obj[pro] + "%";
                }
            });
        }
    }
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>
